.content{
	min-height: 100vh;
	position: relative;

}
.all{
	display: flex;
	flex-direction: row;
}
.container{
  max-width: 100%;
  padding-bottom: 90px;
}
.main{
  max-width: 70%;
  margin: 0 auto;
}
.header{
  background-color: #989898;
  font-size: 18px;
  top:30px;
}
.header ul{
  list-style: none;
  
}
.header ul li{
  display: inline-block;
  padding: 10px;
}
.header a{
  text-decoration: none;
  color: black;
}
.header li:hover{
  background-color: white;
}
.slider {
  display: flex;
  flex-direction: row;
  border-bottom:4px solid red;
  max-width: 620px;
}
.image{
  font-size: 0;
}
.description{
  background-color: #f7f9fa;
  height: 250px;

}
.description h2{
	color: red;
	font-size: 28px;
	padding:10px 15px;
}
.description p{
	font-size: 20px;
	padding: 0 5px;
}
.main_news{
	width: 100%;
}
.main_news p{
	max-width: 142px;
}
.h{
	font-size: 30px;
	padding-left: 30px;
	white-space: nowrap;
}
.line{
	border-bottom: 2px solid black;
	font-size: 0;
	width: 80%;
}
.underline{
	display: flex;
	flex-direction: row;
}
.main_news ul{
	display: flex;
	flex-direction: row;
	list-style: none;
}
.main_news li{
	max-width: 20%;
	padding-right: 10px;
}
.second_block{
	display: flex;
	flex-direction: row;
}
.last_news ul{
	line-height: 20px;
	letter-spacing: -0.01em;
	font-size: 14px;
	line-height: 20px;
	font-weight: 700;
	color: #bfbfc0;
}
.newsfeed ul{
	font-size: 14px;
	line-height: 1.2;
	font-weight: 400;
	letter-spacing: -0.025em;
	color: #333333;
}

.color .h {
	color: red;
}

.color .line {
	border-color: blue;
}
.video_column{
	display: flex;
	flex-direction: row;
}
.video_column p{
	height: 90px;
}
.video p{
font-size: 14px;
line-height: 20px;
font-weight: 700;
letter-spacing: -0.01em;
color: #333333;
max-width: 138px;
}
.more{
	border-bottom: 3px solid black;
	height: 20px;
	width: 100%;
}
.interesting_column{
	display: flex;
	flex-direction: row;
}
.interesting_column p{
height: 68px;
}
.interesting p{
	font-size: 14px;
	line-height: 20px;
	font-weight: 700;
	letter-spacing: -0.01em;
	color: #333333;
	max-width: 138px;
	}

.footer {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 40px;
}
